:local(.container) {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

  :local(.container) :local(.title) {
    position: absolute;
    top: 5%;
    right: 5%;
    display: block;
    font-weight: bold;
    font-size: xx-large;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    z-index: 10;
  }

  :local(.container) :local(.play-button) {
    position: absolute;
    bottom: 5%;
    left: 5%;
    display: block;
    font-weight: bold;
    font-size: xx-large;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    z-index: 10;
    -webkit-transform: perspective(180px) rotateY(23deg);
            transform: perspective(180px) rotateY(23deg);
  }

  :local(.container) :local(.back-button) {
    position: absolute;
    bottom: 5%;
    right: 5%;
    display: block;
    font-weight: bold;
    font-size: xx-large;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    z-index: 10;
    -webkit-transform: perspective(180px) rotateY(-23deg);
            transform: perspective(180px) rotateY(-23deg);
  }

  :local(.container) :local(.disc-list) {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-transition: -webkit-transform 200ms cubic-bezier(0, 0, 0, 1);
    transition: -webkit-transform 200ms cubic-bezier(0, 0, 0, 1);
    transition: transform 200ms cubic-bezier(0, 0, 0, 1);
    transition: transform 200ms cubic-bezier(0, 0, 0, 1), -webkit-transform 200ms cubic-bezier(0, 0, 0, 1);
  }
